<template>
  <div class="admin">
    <h1>Gesti칩n de Alumnos</h1>
    <div class="fileInput">
      <label class="fileLabel"> Subir archivo nuevos alumnos </label>
      <input
        type="file"
        class="csvInput"
        accept=".csv"
        ref="listStudents"
        @change="getFile"
      />
    </div>
    <div class="exportButtons">
      <button class="buttonMap" @click="exportToCSV">
        Exportar datos mapa
      </button>
      <button class="buttonSignUp" @click="goToSignUp">
        Registrar nuevo profesor
      </button>
      <button class="buttonSignUp" @click="mapForms">
        Exportar cuestionarios
      </button>
    </div>
    <table>
      <thead>
        <th>Email</th>
        <th>Nombre</th>
        <th>Clase expositiva</th>
        <th>Practicas de Aula</th>
        <th>Practicas de Laboratorio</th>
      </thead>
      <tr v-for="student in listBehaviour" :key="student.email">
        <td>{{ student.email }}</td>
        <td>{{ student.name }}</td>
        <td>{{ student.expoClass }}</td>
        <td>{{ student.class }}</td>
        <td>{{ student.labClass }}</td>
      </tr>
    </table>
  </div>
</template>
<script>
import API from "../API.js";

const titleNoAllowed = "LISTADO DE ALUMNOS MATRICULADOS";

export default {
  name: "Admin",
  data() {
    return {
      listBehaviour: [],
      titleNoAllowed,
      listForms: [],
    };
  },
  async created() {
    const response = await API.getListStudents();
    response.data.students = response.data.students.filter(student => !student.isAdmin)
    this.listBehaviour = response.data.students.map((student) => {
      return {
        email: student.email,
        name: student.nombre,
        expoClass: student.clasesExpositivas,
        class: student.practicasDeAula,
        labClass: student.practicasDeLaboratorio,
        arrayClicks: student.arrayClick,
      };
    });
  },

  methods: {
    getFile() {
      const files = this.$refs.listStudents.files[0];
      this.$papa.parse(files, {
        delimiter: ";",
        encoding: "UTF-8",
        complete: (results) => this.parseCSV(results.data),
      });
    },
    isValidRow(row) {
      return !row[0] || row[1] === "DNI" || row[0] === titleNoAllowed;
    },
    parseCSV(CSV) {
      const students = [];
      CSV.forEach((row) => {
        if (this.isValidRow(row)) return;

        let student = {
          numero: row[0],
          dni: row[1],
          nombre: row[2],
          email: row[3],
          convocatorias: row[4],
          matriculas: row[5],
          evalucionDiferenciada: row[6],
          erasmus: row[7],
          clasesExpositivas: row[8],
          practicasDeAula: row[9],
          practicasDeLaboratorio: row[10],
          tutoriasGrupales: row[11],
        };
        students.push(student);
      });
      API.sendCSV(students);
    },
    async mapForms() {
      const response = await API.getListStudents();
      response.data.students = response.data.students.filter(student => !student.isAdmin)
      this.listForms = response.data.students.map((student) => {
        console.log(student.form)
        return {
          email: student.email,
          form: student.form,
        };
      });

      const respuestas = {
        'numberSelected': '쮺u치l es el menor n칰mero de colores que has empleado para colorear el mapa?',
        'lessNumOptions': '쮺rees que podr칤a colorearse con un n칰mero de colores menor al que t칰 has empleado?',
        'whyLessNumOptions': '쯇or qu칠? Justifica la respuesta.',
        'strategySelected': '쯈u칠 estrategia has utilizado para resolver el juego?',
        'isMathsRelated': '쮺rees que el juego tiene alguna relaci칩n con las matem치ticas?',
        'difficultyLevel': 'Punt칰a del 1 al 5 la dificultad del juego.',
        'mathsLikesNumber': 'Punt칰a del 1 al 5 tu gusto por las matem치ticas.',
      }
      const array = this.listForms.map(student => {
        const studentFormated = {
          email: student.email
        }
        for(const question in student.form) {
          studentFormated[respuestas[question]] = student.form[question]
        }
        return studentFormated
      })
      var csvForms = this.$papa.unparse(array)
      this.$papa.download(csvForms, 'CSVForms')
      console.log(array)
    },
    goToSignUp() {
      this.$router.push("/signUp");
    },
    async exportToCSV() {
      let copyListBehaviour = this.listBehaviour
      const arrayCSV = copyListBehaviour.map((student) => {
        const studentParsed = { ...student };
        studentParsed.actions = "";
        student.arrayClicks.forEach((eachAction) => {
          if (eachAction.action === "colorClicked") {
            studentParsed.actions += `Color seleccionado: ${eachAction.colorClicked} fecha: ${eachAction.date} \n\r, `;
          }
          if (eachAction.action === "clickRegion") {
            studentParsed.actions += `Comunidad pintada: ${eachAction.regionClicked} fecha: ${eachAction.date} color: ${eachAction.colorPainted} \n\r, `;
          }
          if (eachAction.action === "Continue to form") {
            studentParsed.actions += `Continuar al formulario fecha: ${eachAction.date} \n\r, `;
          }
          if (eachAction.action === "Reset map") {
            studentParsed.actions += `Resetear mapa fecha: ${eachAction.date} \n\r, `;
          }
        });

        delete studentParsed.arrayClicks;
        return studentParsed;
      });

      var csvToDowloand = this.$papa.unparse(arrayCSV);
      this.$papa.download(csvToDowloand, "CSV");
    },
    // parseActions(actions) {
    //   // action, date, colorClicked, colorPainted

    // }
  },
};
</script>

